import BScroll from "better-scroll";
export default {
  name: "timeTransverse",

  props: {
    numbers: {
      //每一次显示多少天数或者周数
      type: Number,
      default: 14
    },
    type: {
      //以周为单位，还是天
      type: String,
      default: "day"
    },
    //第几周
    //默认为当前周
    //上一周:-1，下一周:1。已次类推
    week: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      slide: null,
      liIndex: 0,
      timeDate: []
    };
  },
  mounted() {
    this.timeDate = this.getWeekDay(this.numbers, this.type);
    this.$nextTick(() => {
      this.setTabWidth();

      this.slide = new BScroll(this.$refs.roll, {
        scrollX: true,
        scrollY: false,
        bounce: false,
        click: true
      });
    });
  },
  methods: {
    getWeekDay(day, type = "day") {
      let i = 1,
        time = [];

      this.liIndex = 0;

      if (type === "day") {
        for (i; i <= day; i++) {
          time.push(this.getDay(i));
        }
      } else if (type === "week") {
        day = this.getWeek(7 * day);

        for (i; i < day.length; i++) {
          let num = day[i];

          time.push(this.getDay(num));

          if (num === 0) this.liIndex = i;
        }
      }

      return time;
    },
    getWeek(day) {
      let week = [];
      let today = new Date();
      let tWeek = today.getDay();

      tWeek = tWeek === 0 ? 7 : tWeek;

      for (let i = 0; i < day; i++) {
        week.push(i - tWeek + this.week * day);
      }

      console.log(week);

      return week;
    },
    getDay(day) {
      let today = new Date();

      let targetday_milliseconds = today.getTime() + 1000 * 60 * 60 * 24 * day;

      today.setTime(targetday_milliseconds);

      let tYear = today.getFullYear();
      let tMonth = today.getMonth();
      let tDate = today.getDate();
      let tWeek = today.getDay();

      tMonth = this.doHandleMonth(tMonth + 1);
      tDate = this.doHandleMonth(tDate);
      tWeek = this.hanZiWeek(tWeek);

      return {
        year: tYear + "-" + tMonth + "-" + tDate,
        date: tMonth + "-" + tDate,
        week: "星期" + tWeek
      };
    },
    hanZiWeek(z) {
      let w = {
        1: "一",
        2: "二",
        3: "三",
        4: "四",
        5: "五",
        6: "六",
        0: "日"
      };

      return w[z] || "";
    },
    doHandleMonth(month) {
      let m = month;
      if (month.toString().length == 1) {
        m = "0" + month;
      }
      return m;
    },
    getOffsetRect(element) {
      let elementRect = {
        width: element.offsetWidth,
        height: element.offsetHeight,
        left: element.offsetLeft,
        top: element.offsetTop
      };

      elementRect.right = elementRect.left + elementRect.width;
      elementRect.bottom = elementRect.top + elementRect.height;
      return elementRect;
    },
    setTabWidth() {
      let div = this.$refs.tab.querySelectorAll(".time-tab-item");
      let width = div[0].offsetWidth;
      let len = div.length;

      this.$refs.tab.style.width = width * len + "px";
    },
    handleSwitch(event, obj, index) {
      this.liIndex = index;

      this.$emit("time-click", obj, this.liIndex);
    }
  },
  computed: {
    left() {
      return this.liIndex * 8;
    }
  },
  watch: {
    week(num) {
      this.timeDate = this.getWeekDay(this.numbers, this.type);
    }
  }
};



