
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import scroll from '@/components/scroll/scroll';
import timeTransverse from '@/components/timeTransverse/timeTransverse';
export default {
components:{
XHeader,InlineLoading,timeTransverse,scroll
},
data(){
return {
isloading:false,
list:[],
ksmc: '',
ysbm: '',
ksbm: '',
type: '',
cbm: '',
pageIndex:1,  // 页码
pageSize:10,  // 一页显示几条
qryTime:'',
noData: false,
}
},
beforeRouteLeave(to, from, next){  // 用于做缓存

if (to.name == 'doctorDetails') {
localStorage.setItem('ksmc', this.ksmc)
localStorage.setItem('ysbm', this.ysbm)
localStorage.setItem('type', this.type)
localStorage.setItem('ksbm', this.ksbm)
localStorage.setItem('jgcbm', this.cbm)
}else{
localStorage.removeItem('ksmc')
localStorage.removeItem('ysbm')
localStorage.removeItem('type')
localStorage.removeItem('ksbm')
localStorage.removeItem('jgcbm')
}
next();
},
mounted(){
this.ksmc = this.$route.params.ksmc || window.localStorage.ksmc;  
this.ysbm = this.$route.params.ysbm || window.localStorage.ysbm;  
this.ksbm = this.$route.params.ksbm || window.localStorage.ksbm;  
this.type = this.$route.params.type || window.localStorage.type;  
this.cbm = this.$route.params.cbm || window.localStorage.jgcbm;  

let curDate = new Date();
var nextDate = new Date(curDate.getTime() + 24*60*60*1000); 
this.qryTime = nextDate.getFullYear() + '-' + (nextDate.getMonth() + 1) + '-' + (nextDate.getDate());
this.init();
},
methods:{
init(pullingUp){
this.$ajax.post(this.$root.host,{
"MSH.1": "doctorregistration",
"MSH.2": "listDoctor",
'ksmc': this.ksmc,  // 科室名称
'cbm': this.cbm,  // 医院编码
'pbsj': this.qryTime,  // 时间
"ksbm": this.ksbm,
"type": this.type,
"ysbm": this.ysbm,
'pageNumber': this.pageIndex,  // 页面
'pageSize': this.pageSize,  // 一页显示几条
'token':this.$root.loginInfo.token,

}).then(res => {
if(pullingUp){
this.$refs.scroll.finishPullUp();
if(typeof res.rows != 'undefined' && res.rows.length > 0){
this.list = this.list.concat(res.rows);
}else{
this.$vux.toast.text("没有更多了", "bottom");
}
}else{
if(typeof res.rows != 'undefined' && res.rows.length > 0){
this.list = this.list.concat(res.rows);
}
}
if(this.list.length == 0){
this.noData = true;
}else{
this.noData = false;
}


}).catch(error => {
this.noData = true;

})
},
timeQryFn(obj){
this.qryTime = obj.year;
this.list = [];
this.init();
},
getCZYJSZC(str){
switch(str){
case '01':
return '主任医师'
break;
case '02':
return '副主任医师'
break;
case '03':
return '主治医师'
break;
case '04':
return '医师'
break;
case '05':
return '医士'
break;

}
},
pullingUp(){
this.pageIndex ++;
this.init(true);
}
}
}



